	module.exports = {"data":{"markdownRemark":{"html":"<p>For simplicity, \"Maze Makuro\". First of all it is said to make a maze on the edit screen and play on the game screen.</p>\n<p>But what I thought was that there was no taste alone was to say <em>\"hide the goal\"</em> . You can not goal unless you use the command to inquire about the goal, and if you miss the goal point three times, the game is over. It became <strong>the worst homebrew game in my life history</strong> that no longer can anyone clear.</p>\n<p>...... Oh scary scary (lol</p>\n<ul>\n<li>Genre: Game Construction</li>\n<li>Development language: N88 - Japanese BASIC (86)</li>\n<li>Recommended environment: intel 80286 12 MHz or higher</li>\n<li>It is not distributed because of source loss.</li>\n</ul>","frontmatter":{"cover":null,"date":"1995-10-01T00:00:00+09:00","strDate":"1995/9/30","title":"DANGEON MAKER"}}},"pathContext":{"path":"/en/blog/1995/10/01-dangeon-maker/","langKey":"en"}}

